
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';

export default ({
name:'Edit',
props:['id'],
components:{
Group,XTextarea,mavonEditor,XButton,Toast
},
data:function(){
return {
topic:{
title:'',
content:'',
},
toolbars:{
bold: true, // 粗体
italic: true, // 斜体
header: true, // 标题






ol: true, // 有序列表
ul: true, // 无序列表
link: true, // 链接
imagelink: true, // 图片链接






/* 1.3.5 */


trash: true, // 清空
save: false, // 保存（触发events中的save事件）
/* 1.4.2 */
navigation: true, // 导航目录
/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
/* 2.2.1 */
subfield: true, // 单双栏模式
preview: true 
}
}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.$http.get('/topic/'+to.params.id).then(function(res){

vm.topic=res.data.data;
})
})
},
beforeRouteUpdate:function(to,from,next){
var that = this
this.$http.get('/topic/'+to.params.id).then(function(res){

that.topic=res.data.data;
});
next();
},
methods:{

edittopic:function(){
var that = this;
this.$http.post('/topics/update',{
accesstoken:this.$store.state.accesstoken,
topic_id:this.id,
title:this.topic.title,
content:this.topic.content,
tab:'dev',//类型
}).then(function(res){



that.$router.back();
})
}
}
})



