
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import WechatEmotion from 'vux/src/components/wechat-emotion/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import {mapState} from 'vuex'
export default ({
name:'TopicContent',
props:['id'],
data:function(){
return {
topic:null,//if判断为false
isShowPopup:false,//判断是否弹出弹出框
replyContent:'',
reply_id:null,
reply_loginname:'',
}
},
computed:{
...mapState(['isLogined'])
},
directives: {
TransferDom
},
components:{
Group,
CellBox,
Flexbox,
FlexboxItem,
GroupTitle,
WechatEmotion,
XButton,
Popup,
XTextarea
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.gettopic();
})
},
methods:{

collect:function(){
var that = this;
this.$http.post('/topic_collect/collect',{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id,
}).then(function(res){

that.topic.is_collect=true;
})
},
decollect:function(){
var that = this;
this.$http.post('/topic_collect/de_collect',{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id,
}).then(function(res){

that.topic.is_collect=false;
})
},
upOrDown:function(reply_id){
var that = this;
this.$http.post('/reply/'+reply_id+'/ups',{
accesstoken:this.$store.state.accesstoken,
}).then(function(res){

var type = res.data.action;

if(type=='up'){//判断是否未点赞
var reply;
for(var i=0;that.topic.replies.length;i++){
if(reply_id==that.topic.replies[i].id){//找到点赞的评论
reply=that.topic.replies[i];
break;
}
}

reply.ups.push(that.$store.state.accesstoken);
}else if(type=='down'){
var reply;
for(var i=0;that.topic.replies.length;i++){
if(reply_id==that.topic.replies[i].id){//找到点赞的评论
reply=that.topic.replies[i];
break;
}
}

for(var i=0;reply.ups.length;i++){
if(reply.ups[i]==that.$store.state.accesstoken){//找到对应的点赞的评论
reply.ups.splice(i,1);
break;
}
}

}
})
},

showPopup(replyId,replyLoginname){
this.isShowPopup = true;
this.reply_id=replyId;
this.reply_loginname=replyLoginname;
},

addReply(reply_tab){
var that = this;
var params = {
accesstoken:this.$store.state.accesstoken,
content:this.replyContent,//新建评论的内容
}

if(reply_tab == 2){//判断回复的是其他人的评论
params.reply_id=this.reply_id;
params.content = "@" + this.reply_loginname + " " + params.content;
}
this.$http.post('/topic/'+this.topic.id+'/replies',params).then(function(res){

if(res.data.success){




that.gettopic();
}

that.replyContent='';
that.isShowPopup=false;
that.reply_id='';
that.reply_loginname='';
})
},
gettopic(){
var that = this;
this.$http.get('/topic/'+this.id).then(function(res){

that.topic = res.data.data;
})
}
}
})



