import Vue from 'vue'
import Router from 'vue-router'
import TopicList from '../components/TopicList'
import TopicContent from '../components/TopicContent'
import Login from '../components/Login'
import User from '../components/User'
import ShouCang from '../components/ShouCang'
import Center from '../components/Center'
import Logout from '../components/Logout'
import CreateTopic from '../components/CreateTopic'
import Edit from '../components/Edit'
import Msg from '../components/Msg'


Vue.use(Router)

//以下代码解决路由地址重复点击的报错问题
const originalPush = Router.prototype.push;
Router.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err);
}

export default new Router({
    routes: [
        { path: '/', redirect: '/topiclist/all' },
        { path: '/topiclist/:name', component: TopicList, props: true },
        { path: '/topicContent/:id', component: TopicContent, props: true },
        { path: '/login', component: Login },
        { path: '/user/:loginname', component: User, props: true },
        { path: '/ShouCang/:username', component: ShouCang, props: true },
        { path: '/Center', component: Center },
        { path: '/Logout', component: Logout },
        { path: '/create', component: CreateTopic },
        { path: '/topicContent/:id/edit', component: Edit, props: true },
        { path: '/msg', component: Msg, props: true },
    ]
})


