
import Card from 'vux/src/components/card/index.vue'


export default ({
name:'ShouCangs',
props:['username'],
components:{
Card,
},
data:function(){
return {
collect_topics:null
}
},
beforeRouteEnter:function(to,from,next){
var that = this;
next(function(vm){

vm.$http.get('/topic_collect/'+vm.username).then(function(res){

vm.collect_topics = res.data.data;
})
})
}
})



