
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
import axios from 'axios';
export default {
data:function(){
return {
title:'',
content:'',
toastText:'',
isShowToast:false,//默认不显示警告框
toolbars:{
bold: true, // 粗体
italic: true, // 斜体
header: true, // 标题






ol: true, // 有序列表
ul: true, // 无序列表
link: true, // 链接
imagelink: true, // 图片链接






/* 1.3.5 */


trash: true, // 清空
save: false, // 保存（触发events中的save事件）
/* 1.4.2 */
navigation: true, // 导航目录
/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
/* 2.2.1 */
subfield: true, // 单双栏模式
preview: true 
}
}
},
components:{
Group,XTextarea,mavonEditor,XButton,Toast
},
methods:{
addtopic:function(){
var that = this;
this.$http.post('/topics',{
accesstoken:this.$store.state.accesstoken,
title:this.title,
content:this.content,
tab:'dev',//测试
}).then(function(res){

that.$router.replace('/topiclist/dev');
}).catch(function(error){

that.isShowToast=true;
that.toastText = '内容不能为空或者已经超出限制';
})
},

uploadimage:function(pos,file){
console.log('注意：出现跨域问题，此功能暂未实现')



var formdata = new FormData( );
formdata.append( 'image',file);
var that= this;
axios.post('https://sm.ms/api/v2/upload',formdata,{




headers: {'Content-Type':'multipart/formdata'},//头部消息
}).then(function(res){



that.$refs.md.$img2url(pos,res.data.message.url);





})

}
}
}



