import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        topics: [ //导航tab切换
            { name: 'all', desc: '全部' },
            { name: 'good', desc: '精华' },
            { name: 'share', desc: '分享' },
            { name: 'ask', desc: '问答' },
            { name: 'job', desc: '招聘' },
            { name: 'dev', desc: '测试' },
        ],
        isShowMoreMenus: false, //（弹框）默认显示与否
        // moreMenus: {}, //如果是未登录状态，显示登录；已登录状态，显示个人中心，消息通知，登出 
        isLogined: true, //登录状态，默认登录状态
        unLoginedMenus: { //未登录状态的弹出框菜单
            login: '登录',
        },
        loginedMenus: { //登录状态的弹出框菜单
            center: '个人中心',
            create: '发表新话题',
            msg: '消息通知',
            logout: '登出',
        },
        loginname: 'YT-Yanting', // 登录用户名
        accesstoken: '212ce88b-cae9-411f-9fde-fdefa526d859', // 登录账号（accesstoken值）
        authorId: '615428befe0c515cd3aec367', // 登录账号(作者)id
    },
    getters: {
        moreMenus: function(state) {
            // state 表示当前store对象的state数据
            if (state.isLogined) {
                return state.loginedMenus
            } else {
                return state.unLoginedMenus
            }
        }
    },
    mutations: {
        // mutationsisShowMoreMenus 修改state数据的isShowMoreMenus  表示actionsheet(弹框)的显示与否
        mutationsisShowMoreMenus: function(state, payload) {
            state.isShowMoreMenus = payload;
        },
        // 修改登录状态
        mutationIsLogined: function(state) {
            state.isLogined = true;
        },
        // 修改登录名
        mutationLoginname: function(state, payload) {
            state.loginname = payload;
        },
        // 修改登录账号（accesstoken值）
        mutationAccesstoken: function(state, payload) {
            state.accesstoken = payload;
        },
        // 修改登录账号(作者)id
        mutationAuthorId: function(state, payload) {
            state.authorId = payload;
        },
        // 登出操作
        mutationLogout: function(state) {
            state.isLogined = false;
            state.loginname = '';
            state.accesstoken = '';
            state.authorId = '';
        }
    },
    actions: {},
})

export default store;


