
import {mapState,} from 'vuex';
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default ({
name:'topiclist',
props:['name'],
components:{
Tab,
TabItem,
Cell,
Group,
Scroller
},
computed:{

...mapState(['topics',]),
},



data:function(){
return {
lists:[],
page:1,//当前页数，默认显示第一页
limit:6,//每页显示的记录数量

pullupconfig:{

content:'上拉加载更多',
downContent:'松开后进行加载',
upContent:'加载更多',
loadingContent:'加载中...',
},
pulldownconfig:{

content:'下拉刷新',
downContent:'松开后进行刷新',
upContent:'刷新',
loadingContent:'刷新中...',
}
}
},
beforeRouteEnter(to,from,next){

next(function(vm){


vm.$http.get('/topics',{
params:{
tab:to.params.name,//分类
page:vm.page,//当前页数
limit:vm.limit,//每页的数据数量
}
}).then(function(res){
vm.lists = res.data.data;
})
})
},
beforeRouteUpdate(to,from,next){

var that = this;
this.$http.get('/topics',{
params:{
tab:to.params.name,//分类
page:this.page,//当前页数
limit:this.limit,//每页的数据数量
}
}).then(function(res){

that.lists = res.data.data;
})
},
methods:{
loadMoreDate(){

this.page=this.page+1;
var that = this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit,
}
}).then(function(res){


for(var i=0;i<res.data.data.length;i++){

that.lists.push(res.data.data[i]);
}











that.$refs.pull.donePullup();

})

},
freshDate(){
this.page=1;
var that = this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit,
}
}).then(function(res){

that.lists = res.data.data;

that.$refs.pull.donePulldown();
})
},

routeTo(index){

this.$router.push('/topiclist/'+this.topics[index].name)
},
}
})



