
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
components: {
ButtonTab,
ButtonTabItem,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Group,
Cell,
XButton,
},
data:function(){
return {
hasReadMsgs:null,
hasNotReadMsgs:null,
tabIndex:0,
}
},
created:function(){
this.getAllMsgs();
},
methods:{
getAllMsgs:function(){
var that = this;
this.$http.get('/messages',{
params:{
accesstoken:window.localStorage.getItem('accesstoken'),
}
}).then(function(res){

if(res.data.success){
that.hasReadMsgs = res.data.data.has_read_messages;
that.hasNotReadMsgs = res.data.data.hasnot_read_messages;
}
})
},

markRead:function(mid){
var that=this;
this.$http.post('/message/mark_one/'+mid,{
accesstoken:window.localStorage.getItem('accesstoken')
}).then(function(res){

that.getAllMsgs();
})
},

markAll:function(){
var that=this;
this.$http.post('/message/mark_all',{
accesstoken:window.localStorage.getItem('accesstoken')
}).then(function(res){

that.getAllMsgs();

})
},
}
}



