
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default ({
name:'user',
props:['loginname'],
components:{
Card,Group,Cell
},
data:function(){
return {
userInfo:null,
collect_topics:[],//用户收藏的主题的数组
}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){

vm.$http.get('/user/'+vm.loginname).then(function(res){

vm.userInfo = res.data.data;
})

vm.$http.get('/topic_collect/'+vm.loginname).then(function(res){

vm.collect_topics = res.data.data;
})
})
},
beforeRouteUpdate:function(to,from,next){
var that = this;
this.$http.get('/user/'+to.params.loginname).then(function(res){
that.userInfo = res.data.data;
})

this.$http.get('/topic_collect/'+to.params.loginname).then(function(res){
that.collect_topics = res.data.data;
})
next();
},
})



